:root {
  --body-color: black;
  --text-color: white;
}

body {
  background: var(--body-color);
  color: var(--text-color);
  font-family: 'Gabarito', cursive;
  margin: 0;
}

.container {
  text-align: center;
  display: grid;
  gap: 2rem;
}

.title h1 {
  margin: 0;
  padding-top: 2rem;
}

.board {
  width: fit-content;
  display: flex;
  margin: auto;
}

.boxes {
  display: flex;
  height: 150px;
  width: 150px;
  background: rgb(10, 10, 71);
  border: 5px solid var(--body-color);
  border-radius: 10px;
  font-size: 8rem;
  place-content: center;
  place-items: center;
  cursor: pointer;
}

.boxes:hover {
  background: rgb(11, 11, 98);
}

.Point {
  display: flex;
  gap: 2rem;
  place-content: center;
}

.Point h3 {
  padding: .5rem 1.5rem;
  border: 2px solid white;
}

.reset {
  display: flex;
  place-content: center;
  gap: 2rem;
}

.reset button {
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  border-radius: 5px;
  outline: none;
  border: none;
  font-weight: bold;
  letter-spacing: 1.5px;
  font-size: 15px;
}

.reset button:nth-child(2) {
  background: red;
  color: white;
}

.reset {
  margin-bottom: 2rem;
}

.reset button:hover {
  background-color: rgb(19, 19, 110);
  color: white;
}

.reset button:nth-child(2):hover {
  background: rgb(235, 67, 67);
}

/* ====== Winner ===== */
.winner {
  background: rgba(0, 0, 0, 0.534);
  position: absolute;
  inset: 0;
  display: grid;
  place-items: center;
}

.winner .reset button {
  box-shadow: 0px 0px 10px white;
}

.winner .reset button:hover {
  background: rgb(13, 154, 13);
}

/* Footer  */
footer {
  text-align: center;
  font-size: 12px;
  letter-spacing: 2px;
  color: rgba(255, 255, 255, 0.592);
  position: relative;
  bottom: 0;
  width: 100%;
}

footer span {
  cursor: pointer;
}

@media (max-width:498px) {
  .boxes {
    width: 120px;
    height: 120px;
    font-size: 6rem;
  }
}

@media (max-width:395px) {
  .boxes {
    width: 100px;
    height: 100px;
    font-size: 5rem;
  }
}

@media (max-width:335px) {
  .title h1 {
    font-size: 1.5rem;
  }

  .boxes {
    width: 80px;
    height: 80px;
    font-size: 4rem;
  }
}